import React, { useState, useEffect } from 'react'
import { createClient } from '@supabase/supabase-js'
import Confetti from 'react-confetti'
import { ChevronLeft, ChevronRight, ArrowLeft, Home, Info } from 'lucide-react'
import { Helmet, HelmetProvider } from 'react-helmet-async'

const supabaseUrl = 'https://kjzpfvjyalipijwoesoh.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImtqenBmdmp5YWxpcGlqd29lc29oIiwicm9sZSI6ImFub24iLCJpYXQiOjE3Mjg4MzU3ODMsImV4cCI6MjA0NDQxMTc4M30.JaIDZ6LDvHFa4jptS1BfBTzjMOcAu7ig1PqTHWgqJEg'
const supabase = createClient(supabaseUrl, supabaseKey)

function App() {
  const [currentPage, setCurrentPage] = useState(1)
  const [users, setUsers] = useState([])
  const [loading, setLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('home')
  const [selectedUser, setSelectedUser] = useState(null)
  const [showConfetti, setShowConfetti] = useState(false)
  const usersPerPage = 5 // Reduced to show fewer users per page

  useEffect(() => {
    if (window.Telegram && window.Telegram.WebApp) {
      const tg = window.Telegram.WebApp
      tg.expand()
    }

    const fetchUsers = async () => {
      setLoading(true)
      const { data, error } = await supabase
        .from('users')
        .select('user_id, name, message_count')
        .order('message_count', { ascending: false })
        .limit(30)

      if (error) {
        console.error('Ошибка загрузки пользователей:', error.message)
      } else {
        setUsers(data)
      }
      setLoading(false)
    }

    fetchUsers()

    const preventDefault = (e) => e.preventDefault()
    document.addEventListener('touchmove', preventDefault, { passive: false })
    return () => document.removeEventListener('touchmove', preventDefault)
  }, [])

  const getMedalEmoji = (index) => {
    switch (index) {
      case 0:
        return (
          <div className="flex items-center justify-center w-10 h-10 bg-yellow-500 rounded-full">
            <span className="text-white font-bold text-lg">1</span>
          </div>
        )
      case 1:
        return (
          <div className="flex items-center justify-center w-10 h-10 bg-gray-400 rounded-full">
            <span className="text-white font-bold text-lg">2</span>
          </div>
        )
      case 2:
        return (
          <div className="flex items-center justify-center w-10 h-10 bg-amber-700 rounded-full">
            <span className="text-white font-bold text-lg">3</span>
          </div>
        )
      default:
        return (
          <div className="flex items-center justify-center w-10 h-10 bg-gray-700 rounded-full">
            <span className="text-white font-bold text-lg">{index + 1}</span>
          </div>
        )
    }
  }

  const indexOfLastUser = currentPage * usersPerPage
  const indexOfFirstUser = indexOfLastUser - usersPerPage
  const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser)
  const totalPages = Math.ceil(users.length / usersPerPage)

  const handleUserClick = (user, index) => {
    setSelectedUser(user)
    if (index < 3) {
      setShowConfetti(true)
      setTimeout(() => setShowConfetti(false), 5000)
    }
  }

  const UserPage = ({ user, onBack }) => (
    <div className="flex flex-col h-full bg-gray-900">
      <div className="flex items-center p-4 border-b border-gray-700">
        <button
          onClick={onBack}
          className="text-gray-300 hover:text-white transition-colors duration-200"
        >
          <ArrowLeft className="h-6 w-6" />
        </button>
        <h2 className="ml-4 text-xl font-semibold text-white">{user.name}</h2>
      </div>
      <div className="flex-grow p-4 space-y-4">
        <div className="bg-gray-800 p-4 rounded-xl shadow-sm space-y-2">
          <p className="text-gray-300 text-lg">ID: {user.user_id}</p>
          <p className="text-gray-300 text-lg">Сообщений: {user.message_count}</p>
          <p className="text-gray-300 text-lg">Рейтинг: {users.findIndex(u => u.user_id === user.user_id) + 1}</p>
        </div>
        <div className="bg-gray-800 p-4 rounded-xl shadow-sm space-y-2">
          <h3 className="text-xl font-semibold text-white">Статистика</h3>
          <p className="text-gray-300 text-lg">Среднее в день: {Math.round(user.message_count / 30)}</p>
          <p className="text-gray-300 text-lg">% от общего: {((user.message_count / users.reduce((sum, u) => sum + u.message_count, 0)) * 100).toFixed(2)}%</p>
        </div>
      </div>
    </div>
  )

  const HomePage = () => (
    <div className="space-y-4">
      {loading ? (
        <div className="text-center text-gray-400 py-4 text-xl">Загрузка...</div>
      ) : (
        currentUsers.map((user, index) => {
          const absoluteIndex = indexOfFirstUser + index
          return (
            <div 
              key={user.user_id} 
              className="p-4 bg-gray-800 rounded-xl flex justify-between items-center cursor-pointer hover:bg-gray-700 transition-colors duration-200"
              onClick={() => handleUserClick(user, absoluteIndex)}
            >
              <div className="flex items-center space-x-4">
                {getMedalEmoji(absoluteIndex)}
                <div className="flex flex-col">
                  <span className="font-medium text-white text-lg">{user.name}</span>
                  <span className="text-base text-gray-400">Сообщений: {user.message_count.toLocaleString()}</span>
                </div>
              </div>
            </div>
          )
        })
      )}
      <div className="flex justify-between items-center mt-4">
        <button 
          onClick={() => setCurrentPage(prev => Math.max(prev - 1, 1))}
          disabled={currentPage === 1}
          className="p-2 bg-gray-800 text-gray-300 rounded-full disabled:opacity-50 hover:bg-gray-700 text-xl"
          aria-label="Previous page"
        >
          <ChevronLeft className="h-8 w-8" />
        </button>
        <span className="text-base text-gray-400">
          Страница {currentPage} из {totalPages}
        </span>
        <button 
          onClick={() => setCurrentPage(prev => Math.min(prev + 1, totalPages))}
          disabled={currentPage === totalPages}
          className="p-2 bg-gray-800 text-gray-300 rounded-full disabled:opacity-50 hover:bg-gray-700 text-xl"
          aria-label="Next page"
        >
          <ChevronRight className="h-8 w-8" />
        </button>
      </div>
    </div>
  )

  const InfoPage = () => (
    <div className="space-y-4 text-gray-300 text-lg">
      <h2 className="text-2xl font-bold text-center text-white">О нашем рейтинге</h2>
      <p>
        Наш рейтинг пользователей основан на их активности в чате. Чем больше сообщений отправляет пользователь, тем выше его позиция в рейтинге.
      </p>
      <div className="bg-gray-800 p-4 rounded-xl">
        <h3 className="text-xl font-semibold mb-2 text-white">Как повысить свой рейтинг?</h3>
        <ul className="list-disc list-inside">
          <li>Будьте активны в чате</li>
          <li>Отправляйте полезные сообщения</li>
          <li>Участвуйте в обсуждениях</li>
          <li>Помогайте другим пользователям</li>
        </ul>
      </div>
      <p>
        Рейтинг обновляется автоматически в режиме реального времени. Продолжайте общаться и поднимайтесь выше в списке!
      </p>
    </div>
  )

  return (
    <HelmetProvider>
      <Helmet>
        <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no" />
        <style>
          {`
            html, body {
              touch-action: none;
              overflow: hidden;
              overscroll-behavior: none;
            }
          `}
        </style>
      </Helmet>
      <div className="h-screen bg-gray-900 flex flex-col">
        {showConfetti && (
          <Confetti
            width={window.innerWidth}
            height={window.innerHeight}
            recycle={false}
            numberOfPieces={200}
          />
        )}
        <div className="flex-grow flex flex-col">
          <div className="p-4">
            <h1 className="text-2xl font-bold text-white mb-4">Рейтинг пользователей</h1>
            {selectedUser ? (
              <UserPage user={selectedUser} onBack={() => setSelectedUser(null)} />
            ) : (
              activeTab === 'home' ? <HomePage /> : <InfoPage />
            )}
          </div>
        </div>
        <div className="bg-gray-800 border-t border-gray-700">
          <div className="flex justify-around p-4">
            <button
              className={`flex flex-col items-center ${
                activeTab === 'home' ? 'text-blue-400' : 'text-gray-400'
              }`}
              onClick={() => setActiveTab('home')}
            >
              <Home className="h-8 w-8" />
              <span className="text-base mt-1">Главная</span>
            </button>
            <button
              className={`flex flex-col items-center ${
                activeTab === 'info' ? 'text-blue-400' : 'text-gray-400'
              }`}
              onClick={() => setActiveTab('info')}
            >
              <Info className="h-8 w-8" />
              <span className="text-base mt-1">Информация</span>
            </button>
          </div>
        </div>
      </div>
    </HelmetProvider>
  )
}

export default App